.main-container {
  padding: 0 300px 40px 40px;
}
@media screen and (max-width: 738px) {
  .main-container {
    padding: 0 20px 20px 20px ;
  }
}
.main-container .ant-list-items .ant-list-item {
  padding: 30px 20px 10px 20px;
  border: 1px solid #ebedf0;
  margin-bottom: 20px;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.main-container .ant-list-items .ant-list-item:hover {
  background: #effbff;
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.1);
}
.main-container .ant-list-items .ant-list-item .title {
  font-size: 1.2em;
  color: #394d69;
  cursor: pointer;
  font-weight: 700;
}
.main-container .ant-list-items .ant-list-item .title:hover {
  color: #48b2ff;
}
.main-container .ant-list-items .ant-list-item .time {
  margin-left: 20px;
  font-size: 0.5em;
}
.main-container .preview {
  position: fixed;
  width: 260px;
  top: 104px;
  right: 20px;
  padding: 0 20px;
}
.main-container .preview li {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 14px;
  margin: 10px 0;
  color: #999;
}
.main-container .preview li:hover {
  background-color: #f0f2f5;
}
.main-container .preview li:hover a {
  color: #1890ff;
}
